.market{
    padding: 1% 2% 3% 2%;
    margin: 0 auto;
    background: linear-gradient(0deg, rgba(227,233,255,1) 0%, rgba(211,210,255,1) 50%, rgba(227,233,255,1) 100%);
    background-repeat: repeat;
    font-family: Poppins;
    .market_img{
        width: 46%;
        padding: 2% 4%;
        @media (max-width: 600px){
            display: flex;
            margin: 0 auto;
            width: 90%;
            padding: 3% 4%;
        }
    }
    .market_content{
        width: 92.4%;
        margin: 0 auto;
        .explorer{
            h1{
                margin-top: 4%;
                margin-bottom: 2%;
            }
            .home_gallery{
                margin: 0 auto;
                width: 90%;
                margin-top: 1%;
                @media (max-width: 1024px){
                    width: 100%;
                }
            }
            .load_more{
                background: url('../Assets/home/load_more.svg') no-repeat center;
                background-size: contain;
                margin: 5% 0 3% 0;
                height: 40px;
                width: 100%; 
                border: none;
                color: white;
                font-weight: 500;
                font-size: 20px;
            }
        }
        .top_bids{
            h1{
                display: flex;
                align-items: center;
                img{
                    margin-left: 10px;
                    width: 20px;
                }
            }
        }
    }
}   
.market.dark{
    background: #1e1550;
    .market_content{
        div{
            h1{
                color: white;
            }
        }
    }
}