.eventGallery{
    padding: 1% 2% 3% 2%;
    margin: 0 auto;
    background: linear-gradient(0deg, rgba(227,233,255,1) 0%, rgba(211,210,255,1) 50%, rgba(227,233,255,1) 100%);
    background-repeat: repeat;
    font-family: Poppins;
    .gallery_img{
        display: flex;
        flex-direction: column;
        background: linear-gradient(90deg, #230038e0 6%, #6a00ac7c 16%, #05006a0a 98.65%), url('../Assets/event_gallery/gallery.png'), no-repeat center;
        background-size: 100% 100%;
        width: 92%;
        margin: 0 auto;
        filter: drop-shadow(0px 0px 12px #0057FF);
        border-radius: 4px;
        padding-left: 6%;
        @media (max-width: 1024px){
            background-size: cover;
        }
        h1{
            font-family: Furore;
            font-size: 40px;
            color: white;
            margin-bottom: 0%;
            @media (max-width: 600px){
                font-size: 30px;
            }
        }
        button{
            background: url('../Assets/event_gallery/button.svg') no-repeat center;
            background-size: contain;
            width: 170px;
            height: 100px;
            border: none;
            color: white;
            font-weight: 500;
            font-size: 20px;
            @media (max-width: 600px){
                width: 140px;
                font-size: 15px;
                height: 70px;
            }
        }
    }
    .events{
        position: relative;
        top: 2%;
        width: 98%;
        margin: 0 auto;
        @media (max-width: 1280px){
            width: 98%;
        }
        .event_gallery{
            top: 2%;
            width: 96%;
            margin: 0 auto;
            @media (max-width: 1280px){
                width: 100%;
            }
            a{
                width: 48%;
                margin: 10px auto;
                text-decoration: none;
                @media (max-width: 1280px){
                    width: 70% !important;
                }
                @media (max-width: 800px){
                    width: 60% !important;
                }
                @media (max-width: 600px){
                    width: 70% !important;
                }
                .event_block_wrapper{
                    width: 92% !important;
                    @media (max-width: 1280px){
                        // width: 70% !important;
                    }
                    @media (max-width: 800px){
                        width: 80% !important;
                    }
                    @media (max-width: 600px){
                        width: 90% !important;
                    }
                }
            }
        }
        .load_more{
            background: url('../Assets/home/load_more.svg') no-repeat center;
            background-size: contain;
            margin: 5% 0 3% 0;
            height: 40px;
            width: 100%; 
            border: none;
            color: white;
            font-weight: 500;
            font-size: 20px;
        }
    }
}
.eventGallery.dark{
    background: #1e1550;
    .events{
        h2{
            color: white;
        }
    }
}