.marketplace_slider{
  width: 92.8%;
  margin: 0 auto;

.slick-prev:hover{
  color: none !important;
  outline: none !important;
  background: fixed !important;
}

.slick-next::before, .slick-prev::before{
  content:none !important;
}
.slick-arrow.slick-next {
  background: url('../../Assets/home/arrow_left.svg') no-repeat center;
  background-position: cover;
  transition: 0.4s;
  background-size: cover;
  width: 40px;
  height: 40px;
  z-index: 1;
  right: -4%;
  @media (max-width: 800px){
    background: none ;
    &:hover{
      background: none !important;
    }
  }
}

.slick-arrow.slick-next:hover {
  background: url('../../Assets/home/arrow_right.svg') no-repeat;
  transition: 0.4s;
  background-size: 99% 99% !important;
  width: 40px;
  height: 40px;
  // margin-top: -5px;
  z-index: 1;
}

.slick-arrow.slick-prev {
  background: url('../../Assets/home/arrow_left.svg') no-repeat center;
  background-position: cover;
  transition: 0.4s;
  background-size: cover;
  width: 40px;
  height: 40px;
  z-index: 1;
  left: -4%;
}

.slick-arrow.slick-prev:hover {
  background: url('../../Assets/home/arrow_left.svg') no-repeat !important;
  background-size: 99% 99% !important;
}
@media (max-width: 767px){
