  // }
  .slick-arrow{
    display: none !important;
  }
}

.dark .slick-arrow.slick-next span,
.dark .slick-arrow.slick-prev span {
  color: #fff;
}

.slick-arrow.slick-next:hover {
  background: url('../../Assets/home/arrow_right.svg') no-repeat;
  background-size: 99% 99% !important;
}

body .slick-prev:focus,
.slick-arrow.slick-prev:hover {
  background: url('../../Assets/home/arrow_left.svg') no-repeat;
  background-size: contain;
}
body .slick-next:focus,
.slick-arrow.slick-next:hover {
  background: url('../../Assets/home/arrow_right.svg') no-repeat;
  background-size: contain;
}
@media (max-width: 767.98px) {
  .marketplace_slider {
    padding-bottom: 50px;
  }
  .slick-arrow.slick-next:hover,
  .slick-arrow.slick-prev:hover {
    background: transparent;
    height: 38px;
    color: #797979;
  }
  .slick-arrow.slick-next:hover span,
  .slick-arrow.slick-prev:hover span {
    color: #797979;
  }
  .marketplace_slide {
    margin: 20px 27px;
  }

  .slick-arrow.title {
    max-width: 50%;
  }
  // .marketplace_slide_wrapper::before {
  //   margin-left: 10px;
  //   margin-top: 10px;
  //   width: 220px;
  //   height: 310px;
  // }

  .slick-arrow.slick-next,
  .slick-arrow.slick-prev {
    top: 105%;
  }
  .slick-arrow.slick-prev {
    left: 23px;
  }
  .slick-arrow.slick-next {
    right: 23px;
  }
  // .slick-arrow.slick-dots li button:before {
  //   font-size: 10px;
  // }
  // .dark .slick-arrow.slick-dots li button:before {
  //   font-size: 10px;
  //   color: #fff;
  //   opacity: 0.7;
  // }
  .dark .slick-arrow.slick-dots {
    /* border: 2px solid #fff; */
  }

  // .slick-arrow.slick-dots li.slick-active button:before {
  //   color: #5032ea;
  //   font-size: 14px;
  // }

  // .dark .slick-arrow.slick-dots li.slick-active button:before {
  //   color: #fff;
  //   font-size: 14px;
  //   opacity: 1;
  // }
  .slick-arrow.slick-dots {
    border: 2px solid #797979;
    position: absolute;
    display: block;
    width: 214px;
    margin: 0 auto;
    height: 38px;
    top: 110%;
    left: 50%;
    transform: translateX(-50%);
    padding: 0px 0px 5px 0px;
  }
  .slick-arrow.slick-next,
  .slick-arrow.slick-prev {
    background: none;
    top: 110%;
    height: 38px;
  }
  .slick-arrow.slick-next:hover,
  .slick-arrow.slick-prev:hover {
    margin-top: 0;
  }
  .slick-arrow.slick-next {
    right: 50%;
    border: 2px solid #797979;
    transform: translateX(362%);
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }
  .slick-arrow.slick-prev {
    left: 50%;
    transform: translateX(-362%);
    border: 2px solid #797979;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
  }

  .dark .slick-arrow.slick-next,
  .dark .slick-arrow.slick-prev,
  .dark .slick-arrow.slick-dots {
    color: #fff;
    border-color: #fff;
  }
  // .slick-arrow.slick-next::before,
  // .slick-arrow.slick-prev::before {
  //   content: none;
  // }
  body .slick-next:focus,
  body .slick-next:hover {
    background: none;
  }
  .slick-arrow.slick-next span,
  .slick-arrow.slick-prev span {
    color: #797979;
  }

  .dark .slick-arrow.slick-next span,
  .dark .slick-arrow.slick-prev span {
    color: #fff;
  }
}

@media (max-width: 575.98px) {
  .marketplace_slide {
    padding: 10px;
    margin: 10px 20px 10px 10px;
    height: 330px;
    max-width: 100%;
  }
  .marketplace_slide div a svg {
    width: 21px;
  }

  .marketplace_slide div h6 {
    font-size: 14px;
  }
  .marketplace_slide a img {
    max-width: 20px;
  }
  .marketplace_slide span {
    margin-top: 10px;
    font-size: 14px;
    line-height: 18px;
  }
}

@media (max-width: 425.98px) {
  .marketplace_slide_wrapper:hover::before {
    height: 45%;
  }

  .marketplace_slide span,
  .marketplace_slide span.askar {
    margin-top: 12px;
    font-size: 11px;
    line-height: 15px;
  }

  .marketplace_slide {
    padding: 10px 5px;
    margin: 10px 15px 10px 10px;
    height: 270px;
  }
}
}
.marketplace_slider.dark{
  .slick-arrow.slick-next {
    background: url('../../Assets/right-arrow.png') no-repeat center !important;
    background-size: 99% 99% !important;
  }
  
  .slick-arrow.slick-prev {
    background: url('../../Assets/left-arrow.png') no-repeat center;
    background-size: contain;
  }
  
  .slick-arrow.slick-prev:hover {
    background: url('../../Assets/left-arrow.png') no-repeat !important;
    background-size: 99% 99% !important;
  }
  .slick-dots li.slick-active button{
    color: white !important;
  }
  .slick-dots li.slick-active button::before{
    opacity: .75;
    color: white !important;
  }
  .slick-dots li button::before{
    color: white !important;
  }
}