.event_block_wrapper{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  border-radius: 14px;
  width: 94%;
  margin: 10px auto;
  padding: 10px;
  @media (max-width: 800px){
    border: 1px solid #7E7E7E;
    flex-direction: column;
    width: 70%;
  }
  @media (max-width: 600px){
    width: 70%;
  }
  .event_block{
    display: flex;
    justify-content: center;
    background: url('../../Assets/home/block_border.png') no-repeat center;
    background-size: 99% 99%;
    padding: 15px;
    width: 44%;
    @media (max-width: 800px){
      padding: 15px;
      width: 80%;
      height: 100%;
    }
    div{
      border-radius: 4px;
      overflow: hidden;
      @media (max-width: 800px){
        width: 100%;
      }
      img{
        clip-path: polygon(3% 0, 100% 0, 100% 97%, 97% 100%, 0 100%, 0 3%);
        width: 100%;
        height: 100%;

      }
    }
  }
  .event_info{
    position: relative;
    top: 1.8%;
    width: 48%;
    @media (max-width: 800px){
      top: 10px;
      width: 80%;
      padding-bottom: 2%;
    }
    @media (max-width: 600px){
      width: 90%;
    }
    .author_div{
      clip-path: polygon(25% 0%, 100% 0, 100% 76%, 75% 100%, 0 100%, 0 24%);
      background-color: #616161;
      width: 15%;
      padding: 1px;
      div{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: white;
        padding: 1px;
        clip-path: polygon(25% 0%, 100% 0, 100% 76%, 75% 100%, 0 100%, 0 24%);
        img{
          width: 100%;
          clip-path: polygon(25% 0%, 100% 0, 100% 76%, 75% 100%, 0 100%, 0 24%);
        }
      }
    }
    .nickname{
      color: #0047FF;
      font-size: 16px;
      font-weight: 400;
    }
    h2{
      font-weight: bold;
      font-size: 22px;
      margin: 1% 1% 3% 1%;
      @media (max-width: 800px){
        font-size: 20px;
      }
    }
    .description{
      font-weight: 300;
      font-size: 18px;
      color: #313139;
      margin-top: 2%;
    }
    h2, p{
      color: #313139;
    }
    p{
      font-size: 15px;
      font-weight: 300;
      margin-top: 2%;
    }
    .event_info_footer{
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 96%;
      @media (max-width: 400px){
        width: 100%;
      }
      span{
        display: flex;
        align-items: baseline;
        color: #313139;
        font-size: 16px;
        font-weight: 400;
        @media (max-width: 1420px){
          font-size: 14px;
        }
        @media (max-width: 800px){
          font-size: 12px;
        }
        img{
          width: 12px;
          padding-top: 2%;
          padding-right: 12%;
          &:nth-child(1){
            margin-right: 2%;
          }
        }
      }
      button{
        background: url('../../Assets/home/event_button.svg') no-repeat center;
        background-size: 99% 99%;        
        border: none;
        height: 30px;
        width: 70px;
        color: white;
      }
    }
  }
}
.event_block_wrapper.dark{
  background: #282550;
  .author_div{
    div{
      background-color: #282550;
    }
  }
  .event_info{
    .nickname{
      color: #0047FF !important;
    }
    h2, .description, p{
      color: white !important;
    }
    .event_info_footer span{
      color: white !important;
    }
  }
}