.collection_block_wrapper{
  position: relative;
  max-width: 85%;
  margin: 20px auto;
  @media (max-width: 600px){
    margin: 20px auto;
    max-width: 80%;
  }
  .collection_block {
    background: url('../../Assets/home/block_border.png') no-repeat center;
    background-size: 99% 99%;
    position: relative;
  }

  .collection_block_img {
    padding: 8px;
    border-radius: 10px;
    @media (max-width: 600px){
      padding: 6px;
    }
  }

  .collection_block_img_wrapper {
    background: url('../../Assets/home/Group_1250601.svg') no-repeat center;
    background-size: 100% 100%;
    padding: 10px;
    position: relative;
    overflow: hidden;
    @media (max-width: 600px){
      padding: 6px;
    }
    img{
      width: 100%;
    }
  }
  .dark .collection_block_img {
    background: #282550;
  }

  .collection_block_img img {
    border-radius: 10px;
    transition: 0.4s;
    padding-top: 4px;
  }

  .author{
    display: flex;
    justify-content: center;
    padding: 2px;
    position: relative;
    top: -40px;
    padding-bottom: -40%;
    clip-path: polygon(25% 0%, 100% 0, 100% 76%, 75% 100%, 0 100%, 0 24%);
    background-color: #7E7E7E;
    width: 21%;
    margin: 0 auto;
    img{
      width: 100%;
      clip-path: polygon(25% 0%, 100% 0, 100% 76%, 75% 100%, 0 100%, 0 24%);

    }
  }

  .collection_block_info {
    text-align: center;
    margin-top: -30px;
    padding-bottom: 4px;
  }

  .dark .collection_block_info {
    background: #282550;
  }

  .collection_block_info h6 {
    margin: 0;
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 14px;
    color: #313139;
  }

  .dark .collection_block_info h6 {
    color: #fff;
  }

  .collection_block_footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 18% 0 18%;
  }

  .collection_block_footer span {
    display: flex;
    align-items: center;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 14px;
    color: #7E7E7E;
    img{
      width: 30%;
    }
  }

  .dark .collection_block_footer span {
    color: #ffffff;
  }

  .dark .collection_block_footer span:nth-child(2) {
    color: #0075ff;
  }

  .collection_block_info a {
    font-family: Poppins;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    font-weight: 500;
    line-height: 11.72px;
    color: #0075ff;
    cursor: pointer;
    margin: 2px auto;
    display: block;
  }

  .collection_block_footer .count {
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 11.72px;
  }

  @media (max-width: 991.98px) {
    .collection_main_main .collection_block_wrapper,
    .creator_account_main_block .collection_block_wrapper,
    .product_footer_products .collection_block_wrapper {
      max-width: 28%;
    }
  }

  @media (max-width: 767.98px) {
    .collection_main_main .collection_block_wrapper,
    .creator_account_main_block .collection_block_wrapper,
    .product_footer_products .collection_block_wrapper {
      max-width: 40%;
    }
  }

  @media (max-width: 575.98px) {
    .collection_main_main .collection_block_wrapper,
    .collection_block_wrapper {
      max-width: 280px;
    }

    .creator_account_main_block .collection_block_wrapper,
    .product_footer_products .collection_block_wrapper {
      max-width: 49%;
    }
  }

  @media (max-width: 389.98px) {
    .creator_account_main_block .collection_block_wrapper,
    .product_footer_products .collection_block_wrapper {
      max-width: 280px;
    }
  }
}

.collection_block_wrapper.dark{
  .collection_block_img_wrapper {
    background: url('../../Assets/home/dark_inner_gallery_block.png') no-repeat;
    background-size: 99% 99%;
    padding: 10px;
    position: relative;
    overflow: hidden;
  }
  .collection_block_info{
    h6{
      color: white;
    }
    .collection_block_footer{
      span{
        color: white;
      }
    }
  }
}