.modal_window{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow: auto;
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4);
    color: #EBEBEB;
    z-index: 9999;
    transition: all 1s;
    .window_content{
        background-color: #141233;
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        width: 50%;
        padding: 1% 6%;
        .close_modal{
            display: flex;
            justify-content: flex-end;
            font-size: 40px;
            button{
                background: none;
                border: none;
                color: #818181;
                font-size: 40px;
                cursor: pointer;
            }
        }
        h1{
            margin-top: 0%;
        }
        .image, .name, .url, .description{
            display: flex;
            flex-direction: column;
            width: 90%;
            margin: 2% 0;
            input{
                border: 1px solid #797979;
                background: rgba(255, 255, 255, 0.02);
                box-sizing: border-box;
                backdrop-filter: blur(16px);
                border-radius: 12px;
                height: 46px;
            }
            label{
                margin-bottom: 2%;
            }
        }
        .image{
            img{
                width: 30%;
                background: rgba(255, 255, 255, 0.02);
                border: 1px dashed #797979;
                box-sizing: border-box;
                backdrop-filter: blur(16px);
                padding: 8%;
                border-radius: 12px;
                margin: 3% 0;
            }
            p{
                font-weight: 600;
                font-size: 18px;
            }
            span{
                font-size: 14px;
                color: #A7A7A7;
            }
        }
        .url{
            span{
                color: #A7A7A7;
                font-weight: 300;
                font-size: 14px;
                line-height: 21px;
            }
        }
        .date{
            display: flex;
            justify-content: space-between;
            width: 52%;
            div{
                display: flex;
                flex-direction: column;
                input{
                    border: 1px solid #797979;
                    box-sizing: border-box;
                    backdrop-filter: blur(16px);
                    background: none;
                    border-radius: 12px;
                    height: 46px;
                    width: 8vw;
                    background: rgba(255, 255, 255, 0.02);
                }
                label{
                    margin-bottom: 2%;
                }
            }
        }
        .description{
            margin-bottom: 8%;
            input{
                height: 139px;
            }
        }
        button:nth-last-child(2){
            background: url('../Assets/Group 124914(1).png') no-repeat center;
            background-size: contain;
            color: white;
            border: none;
            width: 163px;
            height: 43px;
            margin: 0 auto;
            font-size: 20px;
        }
        .charity{
            padding: 7% 0 4%;
            text-align: center;
            span{
                font-weight: 300;
                font-size: 14px;
                color: #0075FF;
            }
        }
    }
}