.gallery_block_wrapper.pack_wrapper{
  width: 25%;
  margin: 18px auto;

  @media (max-width: 800px){
    width: 70%;
  }
}
.gallery_block_wrapper {
  position: relative;
  max-width: 24%;
  margin: 20px auto;
  @media (max-width: 1024px){
    max-width: 30%;
  }
  @media (max-width: 800px){
    max-width: 45%;
  }
  @media (max-width: 600px){
    max-width: 70%;
  }

.gallery_main_main .gallery_block_wrapper,
.creator_account_main_block .gallery_block_wrapper,
.product_footer_products .gallery_block_wrapper {
  position: relative;
  max-width: 22%;
  margin: 0 auto 30px;
}

.gallery_block {
  background: url('../../Assets/home/block_border.png') no-repeat center;
  background-size: 99% 99%;
  padding: 10px;
  position: relative;
}
.gallery_block_img {
  padding: 8px;
  background: #fff;
  border-radius: 10px;
}
.gallery_block.dark{
  .gallery_block_img {
    background: #282550 !important;
  }
}

.gallery_block_img_wrapper {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gallery_block.dark{
  .pack{
    .gallery_block_img_wrapper{
      background: url('../../Assets/home/dark_inner_gallery_block.png') no-repeat center !important;
      background-size: contain !important;
    }
  }
}
.pack{
  .gallery_block_img_wrapper{
    background: url('../../Assets/home/galleryInnerShape.png') no-repeat center;
    background-size: 95% 95% !important;
    padding: 6%;
    position: relative;
    img{
      padding: 3%;
      margin-top: 3%;
      height: 189px;
      border-radius: 10px;
      @media (max-width: 800px){
        height: 40vw;
      }
    }
  }
}


.dark .gallery_block_img {
  background: #282550;
}

.gallery_block_img img {
  border-radius: 10px;
  transition: 0.4s;
  height: 219px;
}

.gallery_block_img img:hover {
  transform: scale(1.1);
  transition: 0.4s;
}

.gallery_block_info {
  background: #fff;
  padding: 7px;
  border-radius: 10px;
  margin-top: 10px;
  -webkit-clip-path: polygon(
    0% 0%,
    /* верхняя левая */ 0% 0%,
    /* верхняя левая */ 100% 0%,
    /* верхняя правая */ 100% 0%,
    /* верхняя правая */ 100% 87%,
    /* нижняя правая */ 96% 100%,
    /* нижняя правая */ 0% 100%,
    /* нижняя левая */ 0 100% /* нижняя левая */
  );
  clip-path: polygon(
    0% 0%,
    /* верхняя левая */ 0% 0%,
    /* верхняя левая */ 100% 0%,
    /* верхняя правая */ 100% 0%,
    /* верхняя правая */ 100% 87%,
    /* нижняя правая */ 96% 100%,
    /* нижняя правая */ 0% 100%,
    /* нижняя левая */ 0 100% /* нижняя левая */
  );
}

.dark .gallery_block_info {
  background: #282550;
}

.gallery_block_info h6 {
  margin: 0;
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  color: #000;
}

.dark .gallery_block_info h6 {
  color: #fff;
}

.gallery_block_footer {
  display: flex;
  justify-content: space-between;
}

.gallery_block_footer span {
  display: flex;
  align-items: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  color: #000;
}

.dark .gallery_block_footer span {
  color: #ffffff;
}

.dark .gallery_block_footer span:nth-child(2) {
  color: #0075ff;
}

.gallery_block_info a {
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  color: #0075ff;
  cursor: pointer;
  margin: 5px 0;
  display: block;
}

.gallery_block_footer .count {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  color: #0075ff;
}

@media (max-width: 991.98px) {
  .gallery_main_main .gallery_block_wrapper,
  .creator_account_main_block .gallery_block_wrapper,
  .product_footer_products .gallery_block_wrapper {
    max-width: 28%;
  }
}

@media (max-width: 767.98px) {
  .gallery_main_main .gallery_block_wrapper,
  .creator_account_main_block .gallery_block_wrapper,
  .product_footer_products .gallery_block_wrapper {
    max-width: 40%;
  }
}

@media (max-width: 575.98px) {
  .gallery_main_main .gallery_block_wrapper,
  .gallery_block_wrapper {
    max-width: 280px;
  }

  .creator_account_main_block .gallery_block_wrapper,
  .product_footer_products .gallery_block_wrapper {
    max-width: 49%;
  }
}

@media (max-width: 389.98px) {
  .creator_account_main_block .gallery_block_wrapper,
  .product_footer_products .gallery_block_wrapper {
    max-width: 280px;
  }
}
}