footer{
    display: flex;
    justify-content: space-between;
    background: #E7EFFF;
    height: 124px;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
    padding: 5% 10% 3% 10%;
    z-index: 9999;
    @media (max-width: 800px){
        flex-direction: column;
        height: auto;

    }
    .logo{
        @media (max-width: 800px){
            margin: 0 auto;
        }
        img{
            width: 140px;
        }
    }
    .links{
        display: flex;
        width: 36%;
        margin-right: 10%;
        justify-content: space-between;
        @media (max-width: 800px){
            margin: 0 auto;
            flex-direction: column;
            text-align: center;
            padding-top: 4%;
        }
        div{
            display: flex;
            flex-direction: column;
            @media (max-width: 800px){
                padding-top: 10%;
            }
        }
    }
    .social_media{
        height: auto;
        display: flex;
        width: 30%;
        justify-content: space-between;
        @media (max-width: 800px){
            width: 90%;
            padding-top: 8%;
            margin: 0 auto;
        }
        img{
            height: 35px;
            width: 35px;
        }
    }
}
footer.dark{
    background: #141233;
    color: white;
    .social_media{
        img{
            filter: brightness(0) invert(1);
        }
    }
}