header.header{
    display: flex;
    justify-content: space-between;
    height: 84px;
    background: #E7EFFF;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
    align-items: center;
    padding: 0 2% 0 3%;
    min-height: 60px;
    z-index: 9999;
    @media (max-width: 1024px){
        padding: 1.4% 5% 1.4% 3%;
    }
    @media (max-width: 600px){
        height: 54px;
        padding: 1.4% 7% 1.4% 4%;
    }
    div{
        display: flex;
        align-items: center;
    }
    .logo{
        @media (max-width: 1024px){
            width: 55%;
        }
        @media (max-width: 800px){
            width: 55%;
        }
        @media (max-width: 600px){
            display: flex;
            align-items: center;
            img{
                width: 80%;
            }
        }
    }
    .submenu{
        width: 32%;
        justify-content: space-between;
        font-size: 18px;
        font-weight: 500;
        @media (max-width: 1024px){
            display: none;
        }
        .option_menu{
            width: 54%;
            @media (max-width: 1420px){
                width: 62%;
            }
            select{
                -webkit-appearance: none;
                -moz-appearance: none;
                background: transparent;
                background-image: url("data:image/svg+xml;utf8,<svg fill='black' height='36' viewBox='0 0 24 24' width='36' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
                background-repeat: no-repeat;
                background-position-x: 98%;
                background-position-y: 7px;
                border: none;
                margin-right: 2rem;
                padding: 1rem;
                padding-right: 2rem;
    
                font-size: 14px;
                border: none;
                font-weight: 500;
                font-size: 16px;
                color: #141233;
            }
            @media (max-width: 800px){
                display: none;
            }
        }
    }
    .header_search{
        width: 26%;
        margin-left: 3%;
        @media (max-width: 1200px){
            width: 22%;
        }
        @media (max-width: 1024px){
            width: 2%;
            img{
                width: 26px;
            }
        }
        input {
            background: none;
            border: 1px solid grey;
            border-radius: 10px;
            padding-right: 40px;
            color: black;
            width : 100%;
            height: 36px;
            outline: none;
            @media (max-width: 1024px){
                display: none;
            }
        }
        button{
            background: none;
            border: none;
            position: relative;
            right: 40px;
            @media (max-width: 600px) {
                right: 30px; 
            }
        }
    }
    .buttons{
        justify-content: space-between;
        width: 34%;
        @media (max-width: 800px){
            width: 32%;
        }
        @media (max-width: 600px){
            width: 42%;
        }        
        .mode_switcher{
            background: none;
            div{
                padding: 9px;
                -webkit-animation: slide 0.5s forwards;
                -webkit-animation-delay: 2s;
                animation: slide 0.5s forwards;
                animation-delay: 2s;
                img{
                    width: 20px;
                }
            }
            .day{
                position: relative;
                border: 1px solid #616161;
                border-right: none;
                border-top-left-radius: 10px;
                border-bottom-left-radius: 10px;
                overflow: hidden;
                width: 24px;   
                @media (max-width: 1200px){
                    display: none;
                }
            }
            .night{
                position: relative;
                border: 1px solid #616161;
                border-left: none;
                border-top-right-radius: 10px;
                border-bottom-right-radius: 10px;
                overflow: hidden;
                width: auto;   
                @media (max-width: 1200px){
                    display: none;
                }                                   
            }
            .day_on{
                border-right: none;
                border-top-left-radius: 10px;
                border-bottom-left-radius: 10px;  
                background:  linear-gradient(90deg, #5507DF 3.7%, #34E7FD 100%);
            }
            .night_on{
                border: 1px solid #616161;
                border-left: none;
                border-top-right-radius: 10px;
                border-bottom-right-radius: 10px;
                background:  linear-gradient(90deg, #5507DF 3.7%, #34E7FD 100%);
            }
            .day_on, .night_on{
                transition: all 1s;
                position: absolute;

            }
            .adaptive_dark_mode{
                display: none;
                @media (max-width: 1200px){
                    display: flex;
                    img{
                        width: 28px;
                    }
                }
            }
        }

        button{
            display: flex;
            align-items: center;
            justify-content: center;
            border: none;
            cursor: pointer;
            &:nth-child(2){
                height: 41px;
                width: 100px;
                background: url('../Assets/header/Group_124580.svg') no-repeat center;
                background-size: 100% 100%;
                color: white;
                @media (max-width: 1024px){
                    display: none;
                }
            }
        }
        .profile_pic{
            display: flex;
            justify-content: center;
            border: 1px solid #616161b4;
            border-radius: 6px;
            background-color: #E7EFFF;
            padding: 2px 2px 2px 2px;
            img{
                background-color: #E4E4E4;
                border-radius: 10px;
                width: 36px;
                height: 34px;
            }
        }
        .icon{
            clip-path: polygon(25% 0%, 100% 0, 100% 76%, 75% 100%, 0 100%, 0 24%);
            background-color: #6161616c;
            padding: 1px;
            @media (max-width: 1024px){
                display: none;
            }
            button{
                border: none;
                clip-path: polygon(25% 0%, 100% 0, 100% 76%, 75% 100%, 0 100%, 0 24%);
                background-color: #E7EFFF;
                padding: 1.8px;
                img{
                    clip-path: polygon(25% 0%, 100% 0, 100% 76%, 75% 100%, 0 100%, 0 24%);
                    background-color: #E4E4E4;
                    padding: 3px;
                    height: 26px;
                }
            }
        }
        .adaptive_menu{
            display: none;
            @media (max-width: 1024px){
                display: flex;
                img{
                    margin-left: 10%; 
                    width: 80%;
                }
            }
        }
    }
}
.adaptive_submenu{
    position: absolute;
    top: 98px;
    left: 0;
    transition: all 1s;
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
    background: #E9F1FF;
    @media (max-width: 600px){
        top: 75px;
    }
    a{
        width: 100%;
        font-weight: 800;
        font-size: 25px;
        padding: 2% 4% 2% 20%;
        border-bottom: 1px solid black;
        text-decoration: none;
    }
    div{
        display: flex;
        justify-content: space-between;
        width: 90%;
        padding: 2%;
    }
    button{
        display: flex;
        justify-content: center;
        align-items: center;
        border: none;
        height: 40px;
        width: 120px;
        &:nth-child(1){
            background: url('../Assets/header/nft_button.png') no-repeat center;
            background-size: contain;
            color: white;
            &:hover{
                background: url('../Assets/header/button.png') no-repeat center;
                color: black;
                background-size: contain;
            }
        }
        &:nth-child(2){
            background: url('../Assets/header/button.png') no-repeat center;
            background-size: contain;
            &:hover{
                color: white;
                background: url('../Assets/header/nft_button.png') no-repeat center;
                background-size: contain;
            }
        }
    }
}
header.header.dark{
    background: #141233 !important;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
    color: white;
    select{
        color: white !important;
        background-image: url("data:image/svg+xml;utf8,<svg fill='white' height='36' viewBox='0 0 24 24' width='36' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>") !important;
    }
    .profile_pic{
        background-color: #141233 !important;
        img{
            background-color: #060028 !important;
        }        
    }
    .icon{
        button{
            background-color: #141233 !important;
            img{
                background-color: #060028 !important;
            }
        }
    }
    .adaptive_menu{
        img{
            filter: brightness(0) invert(1);
        }
    }
    .adaptive_submenu{
        background: #141233 !important;
        a{
            border-bottom: 1px solid white;
            color: white;
        }
        button{
            color: white;
        }
        img{
            filter: brightness(0) invert(1);
        }    
    }
}