.seller_block_wrapper{
  display: flex;
  justify-content: center;
  background: url('../../Assets/home/seller_shape.svg') no-repeat center;
  background-size: contain;
  width: 80%;
  margin: 10px auto;
  padding: 8px;
  @media (max-width: 600px){
    margin: 20px auto;
    padding: 5px;
    width: 88%;
  }
  .seller_inner{
    display: flex;
    justify-content: center;
    background: url('../../Assets/home/seller_inner_shape.png') no-repeat center;
    background-size: contain;
    width: 98%;
    @media (max-width: 600px){
      width: 91%;
      height: 42px;
    }
    .seller_inner_block{
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 90%;
      .seller{
        width: 28%;
        img{
          width: 100%;
        }
      }
      .seller_info{
        display: flex;
        flex-direction: column;
        width: 64%;
        h4{
          font-size: 20px;
          margin: 10% 0 10% 0;
          @media (max-width: 600px){
            font-size: 15px;
            margin: 6% 0 6% 0;
          }
          @media (max-width: 400px){
            font-size: 13px;
            margin: 6% 0 6% 0;
          }
        }
        .seller_footer{
          display: flex;
          justify-content: space-between;
          width: 86%;
          span{
            display: flex;
            align-items: center;
            margin-bottom: 10%;
            flex-shrink: 0;
            @media (max-width: 600px){
              font-size: 11px;
              img{
                width: 30%;
              }
            }
          }
        }
      }
    }
  }
}
.seller_block_wrapper.dark{
  .seller_inner{
    display: flex;
    justify-content: center;
    background: url('../../Assets/home/dark_seller_inner_shape.png') no-repeat center;
    background-size: contain;
    @media (max-width: 600px){
      width: 91%;
      height: 42px;
    }
    .seller_info{
      h4{
        color: white;
      }
      span{
        color: white;
      }
    }
  }
}
