.event_component{
    padding: 2% 10% 3% 10%;
    margin: 0 auto;
    background: linear-gradient(0deg, rgba(227,233,255,1) 0%, rgba(211,210,255,1) 50%, rgba(227,233,255,1) 100%);
    background-repeat: repeat;
    font-family: Poppins;
    @media (max-width: 600px){ 
        padding: 4% 10% 3% 10%;
    }
    a{  
        color: #313139;
        text-decoration: none;
        font-weight: 600;
        margin-left: 1%;
        
    }
    .event_block_wrapper{
        margin-top: 3%;
        align-items: flex-start;
        justify-content: start;
        width: 100%;
        background: none;
        border: none;
        .event_block{
            width: 30% !important;
            background: url('../Assets/event_gallery/shape.png') no-repeat center;
            background-size: contain;
            @media (max-width: 1024px){ 
                width: 45% !important;
            }
            @media (max-width: 600px){ 
                width: 50% !important;
            }
            @media (max-width: 400px){ 
                width: 70% !important;
            }
            img{
                width: 24vw;
                @media (max-width: 1024px){ 
                    width: 35vw !important;
                }
                @media (max-width: 600px){ 
                    width: 40vw !important;
                }
                @media (max-width: 400px){ 
                    width: 60vw !important;
                }
            }
        }
        .event_info{
            top: 0 !important;
            height: 100%;
            width: 45%;
            margin-left: 4%;
            justify-content: space-between;
            @media (max-width: 1024px){ 
                margin: 2% 1%;
                // margin-left: 1%;
                width: 90%;
            }
            h2{
                margin: 0 0 7%;
                @media (max-width: 1024px){ 
                    margin: 0 0 2%;
                }
            }
            .event_info_footer{
                margin-top: 4%;
                width: 30% !important;
                @media (max-width: 1024px){ 
                    width: 40% !important;
                    span{
                        font-size: 15px;
                    }
                }
                @media (max-width: 600px){ 
                    width: 56% !important;
                }
                @media (max-width: 400px){ 
                    width: 66% !important;
                }
            }
            button{
                background: url('../Assets/event_gallery/button.svg') no-repeat center;
                background-size: contain;
                width: 120px;
                height: 50px;
                border: none;
                color: white;
                font-weight: 400;
                font-size: 16px;
                margin: 3% 0;
            }
        }
    }

    .explorer{
        width: 100%;
        margin: 0 auto;
    }
}
.event_component.dark{
    background: #1e1550;
    color: white;
    h2{
        color: white;
    }
}