.filters_wrapper{
    display: flex;
    justify-content: space-between;
    margin-bottom: 1%;
        width: 92.4%;
    margin: 0 auto; 
    @media (max-width: 1024px){
        display: none;
    }
    .filters{
        display: flex;
        justify-content: space-between;
        width: 65%;
    }
    .active.filter{ 
        background: url('../../Assets/home/filter_active.svg') no-repeat center;
        background-size: 100% 100%;
        color: white;
        &:after {
            position: absolute;
            margin-left: 6px;
            margin-bottom: 6px;
            content: '';
            width: 80px;
            border-radius: 10px;
            height: 41px;
            background-color: white;
            opacity: 0.2;
            
          }
    }
    .filter{
        background: url('../../Assets/home/filter.svg') no-repeat center;
        background-size: 100% 100%;
        color: #313139;
    }
    .filter, .active.filter, .charity{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 89px;
        height: 43px;
        cursor: pointer;
        border: none;
    }
    .charity{
        background: url('../../Assets/home/charity.svg') no-repeat center;
        background-size: 100% 100%;
        color: white;
    }
}
.filters_wrapper.dark{
    button{
        color: white !important;
        img{
            filter: brightness(0) invert(1);
        }
    }
}