.home{
    padding: 3% 4% 3% 4%;
    margin: 0 auto;
    background: linear-gradient(0deg, rgba(227,233,255,1) 0%, rgba(211,210,255,1) 50%, rgba(227,233,255,1) 100%);
    background-repeat: repeat;
    font-family: Poppins;
    transition: all 1s;
    @media (max-width: 800px){
        padding: 3% 6% 3% 6%;
    }
    @media (max-width: 420px){
        padding: 3% 4% 3% 4%;
    }
    .own_nft_n_examples{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 30vw;
        @media (max-width: 600px){
            height: 45vw;
        }
        .ur_own_ntf{
            display: flex;
            width: 70%;
            height: 100%;
            align-items: center;
            justify-content: space-between;
            border-radius: 20px;
            background-position: center;
            background-repeat: no-repeat; 
            background-size: cover; 
            color: white;
            @media (max-width: 800px){
                width: 65%;
            }
            @media (max-width: 600px){
                width: 100%;
                height: 100%;
            }
            img{
                width: 90%;
                height: 100%;
                @media (max-width: 600px){
                    width: 80vw;
                    height: 85%;
                    padding-top: 7%;
                }
            }
            button{
                background: url('../Assets/header/Group_124580.svg') no-repeat center;
                background-size: contain;
                width: 64%;
                margin-top: 14%;
                font-size: 17px;
                height: 55px;
                border: none;
                color: white;
                @media (max-width: 1024px){
                    margin-top: 3%;
                    margin-left: 12%;
                    width: 50%;
                    font-size: 16px;
                }
                @media (max-width: 600px){
                    margin-top: 3%;
                    margin-left: 12%;
                    width: 64%;
                    font-size: 15px;
                }
            }
            h1{
                padding-left: 10%;
                font-family: Furore;
                font-size: 50px;
                width: 140%;
                color: white;
                @media (max-width: 1024px){
                    font-size: 36px;
                }
                @media (max-width: 800px){
                    padding-bottom: 5%;
                    font-size: 36px;
                    width: 135%;
                }
                @media (max-width: 600px){
                    font-size: 32px;
                    width: 130%;
                }
            }
        }
        .nft_example{
            position: relative;
            flex-direction: column;
            justify-content: center;
            height: 100%;
            width: 28%;
            border-radius: 14px;
            background-repeat: no-repeat;
            background-size: 100% 100%;
            background-position: center top;
            color: white; 
            @media (max-width: 800px){
                width: 32%;
            }
            @media (max-width: 600px){
                display: none;
            }
            span{
                position: relative;
                top: 10%;
                margin-left: 10%;
                font-weight: bold;
                font-size: 24px;
                line-height: 36px;
                @media (max-width: 800px){
                    font-size: 20px;
                }
            }
            div{
                display: flex;
                justify-content: center;
                position: relative;
                top: 79%;
                @media (max-width: 1024px) {
                    top: 74%;
                }
                @media (max-width: 800px){
                    top: 66%;
                }
                button{
                    width: 23%;
                    height: 4px;
                    background-color: white;
                    border: none;
                    margin: 10px;
                    cursor: pointer;
                }
            }     
        }
    }
    .nft_demo{
        display: flex;
        justify-content: space-between;
        margin-top: 2%;
        div{
            background-position: center;
            background-repeat: no-repeat; 
            background-size: cover;    
            border-radius: 14px;   
            width: 18%;
            height: 210px;  
            border: 0.2px solid #7d9cd2;  
            @media (max-width: 800px){
                width: 30%;
                height: 28vw;  
                font-size: 13px;
                &:nth-last-child(1), &:nth-last-child(2){
                    display: none;
                }
            }

            span{
                position: relative;
                top: 6%;
                left: 6%;
                color: white;
                font-weight: 600;
            }
        }
    }
    .top_collections{
        @media (max-width: 600px) {
            margin-top: -4%;
        }
        h1{
            display: flex;
            align-items: center;
            margin-bottom: -2%;
            h2{
                display: flex;
                align-items: center;
                background: -webkit-linear-gradient(0deg, rgba(105,193,255,1) 0%, rgba(213,22,255,1) 100%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                margin-left: -4px;
                select{
                    background: none;
                    border: none;
                    font-size: 28px;
                    margin-top: 5px;
                    margin-left: 10px;
                    font-weight: 600;
                    option{
                        font-size: 14px;
                    }
                }
            }
        }
    }
    div{
        h1{
            margin-bottom: -0%;
            color: #313139;
            @media (max-width: 800px){
                margin-top: 10%;
                font-size: 24px;
            }
            select{
                @media (max-width: 800px){
                    margin-top: 0 !important;
                    font-size: 24px !important;
                }          
            }
        }
    }
    .top_bids{
        h1{
            display: flex;
            align-items: center;
            img{
                margin-left: 10px;
                width: 20px;
            }
        }
    }
    .popular_events{
        h1{
            margin-bottom: 1%;
        }     
    }
    .explorer{
        h1{
            margin-top: 4%;
            margin-bottom: 2%;
        }
        .home_gallery{
            margin: 0 auto;
            width: 93%;
            margin-top: 1%;
            @media (max-width: 1024px){
                width: 100%;
            }
        }
        .load_more{
            background: url('../Assets/home/load_more.svg') no-repeat center;
            background-size: contain;
            margin: 5% 0 3% 0;
            height: 40px;
            width: 100%; 
            border: none;
            color: white;
            font-weight: 500;
            font-size: 20px;
        }
    }
}
.home.dark{
    background: #1e1550;
    transition: all 1s;
    .ur_own_ntf, .nft_example{
        border: 0.2px solid #7d9cd2;
    }
    div{
        h1{
            color: white;
        }
    }
}